.price {
  background-color: $grey;
  clip-path: polygon(0% 3.5%, 100% 0%, 100% 100%, 0 100%);
  padding-top: 94px;

  @include tablet() {
    clip-path: polygon(0 11%,100% 0,100% 100%,0 100%);
    padding-top: 41px;
    margin-top: -58px;
  }
}

.price__wrapper {
  @include wrapper;
  padding-left: 0;
  padding-right: 0;
  padding-bottom: 46px;

  @include tablet() {
    padding-top: 58px;
    padding-bottom: 15px;
  }

  @include desktop() {
    padding-top: 91px;
    padding-bottom: 52px;
  }
}

.price__table-wrapper {
  position: relative;
  overflow: hidden;
  padding-bottom: 7px;
}

.price__rate {
  font-weight: 300;
  font-size: 18px;
  line-height: 30px;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: $white;

  @include desktop() {
    font-size: 24px;
  }
}
