.slider__button {
  display: none;

  @include desktop() {
    display: block;
    background-repeat: no-repeat;
    background-color: $white;
    color: $blue;
    opacity: 0.3;
    border: none;
    width: 22px;
    height: 41px;
    position: absolute;
    transform: translateY(380%);
    cursor: pointer;
  }

  &--prev {
    /* background-image: url("../../img/icon-left-arrow.svg"); */
    transform: translateY(-50%);
    top: 49%;
    left: 130px;
  }

  &--next {
    /* background-image: url("../../img/icon-right-arrow.svg"); */
    transform: translateY(-50%);
    top: 49%;
    right: 130px;
    transform: rotate(180deg);
  }

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.1;
  }
}
