.main-footer__wrapper {
  @include wrapper;
  display: flex;
  flex-direction: column;
  align-items: center;

  @include tablet() {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }

  @include desktop() {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 37px;
  }
}

.main-footer__link {
  margin-bottom: 34px;
}

