.main-footer__logo-icon {
  &:hover {
    color: $pink;
  }

  &:active {
    opacity: 0.3;
  }
}

.main-footer__logo-icon--mobile {
  @include tablet() {
    display: none;
  }

  @include desktop() {
    display: none;
  }
}

.main-footer__logo-icon--tablet {
  display: none;

  @include tablet() {
    display: block;
  }

  @include desktop() {
    display: none;
  }
}

.main-footer__logo-icon--desktop {
  display: none;

  @include tablet() {
    display: none;
  }

  @include desktop() {
    display: block;
  }
}

.main-footer__copyright {
  display: flex;
  align-items: center;
}
