.filter__item {
  display: flex;
  align-items: center;

  &--active svg {
    color: $pink;
  }

  &--colour,
  &--contrast {
    opacity: 0.3;
  }

  @include tablet() {
    svg {
      color: $pink;
      width: 22px;
      height: 22px;
      margin-right: 29px;
    }

    &--colour,
    &--contrast {
      opacity: 1;
    }
  }

  @include desktop() {
    svg {
      width: 10%;
      height: 10%;
      margin-right: 47px;
      margin-bottom: 7px;
    }
  }

  &:not(:last-child) {
    margin-right: 35px;

    @include tablet() {
      margin-right: 0;
    }
  }

  @include tablet() {
    width: 100%;
    padding-top: 25px;
    padding-bottom: 25px;
    border-bottom: 1px solid rgba(40, 54, 69, 0.1);
  }

  @include desktop() {
    padding-bottom: 10px;
    padding-top: 13px;

    &:first-child {
      padding-top: 0;
    }
  }
}

.filter__item--active .upload__range-controls-bar {
  position: absolute;
  display: block;
  top: calc(100% + 25px);
  width: 74vw;
  left: 20px;
  margin-top: 10px;

  @include tablet() {
    top: 25px;
    left: 50px;
    width: 74%;
  }

  @include desktop() {
    width: 63%;
    top: 10px;
    left: 90px;
  }
}

.filter__item:first-of-type {
  .range-controls__bar {
    position: absolute;
    display: block;
    bottom: -41px;
    margin-bottom: 0;
    width: calc(100% - 40px);
    left: 0;
    right: 0;

    @include tablet() {
      position: relative;
      width: 74%;
      left: unset;
      right: unset;
      bottom: unset;
    }

    @include desktop() {
      width: 63%;
    }
  }
}

.filter__icon-colour,
.filter__icon-contrast
{
  &:hover {
    color: $blue;
    opacity: 0.5;
  }
}
