.upload__description {
  @include wrapper;
}

.upload__text {
  font-size: 14px;
  line-height: 30px;
  text-align: center;
  color: $blue;
  margin: 0;
  margin-bottom: 46px;

  @include tablet() {
    font-size: 18px;
    margin-bottom: 78px;
  }

  @include desktop() {
    margin-bottom: 75px;
  }
}

.upload__wrapper {
  @include wrapper;
  padding-top: 20px;
  margin-bottom: 40px;
  background-color: $grey;

  @include tablet() {
    padding-top: 57px;
  }

  @include desktop() {
    background-color: $white;
    padding-top: 0;
  }
}

.upload__form-wrapper {
  background-color: $grey;
  display: flex;
  flex-direction: column;
  align-items: center;

  @include tablet() {
    display: grid;
    /* grid-template-columns: calc(45% - 24px) calc(55% - 24px); */
    grid-template-columns: calc(52% - 24px) calc(48% - 24px);
    gap: 51px;
    align-items: start;
  }

  @include desktop() {
    display: grid;
    grid-template-columns: calc(40% - 24px) calc(60% - 24px);
    gap: 91px;
    align-items: start;
    padding: 60px 57px 18px 81px;
    margin-bottom: 100px;
  }
}

.upload__filter {
  @include list-reset;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 18px;
  margin-bottom: 66px;

  @include tablet() {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 19px;
  }

  @include desktop() {
    margin-top: 0;
    margin-bottom: 153px;
  }
}

.upload__container {
  width: 100%;
  max-width: 490px;
}

.upload__image {
  margin-bottom: 42px;

  @include tablet() {
    width: 100%;
  }
}

.upload__buttons-container {
  display: flex;
  flex-direction: column;
  border-top: 1px solid rgba(40, 54, 69, 0.1);
  margin: 0 -20px;
  padding: 47px 20px 29px;

  @include tablet() {
    border-top: 0;
    margin: 0;
    padding: 0;
  }

  @include desktop() {
    flex-direction: row;
    justify-content: space-between;
    margin-right: 40px;
  }
}

.upload__buttons {
  margin-bottom: 12px;
  color: $pink;
  border-color: $pink;

  &:hover {
    color: $white;
    background-color: $pink;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }

  @include tablet() {
    width: 94%;
    margin-bottom: 21px;
  }

  @include desktop() {
    width: 45%;
  }

  &--cancel {
    color: rgba(40, 54, 69, 0.1);    ;
    border: 3px solid rgba(40, 54, 69, 0.1);

    &:hover {
      background-color: $grey;
      border: 3px solid rgba(40, 54, 69, 0.3);
      color: rgba(40, 54, 69, 0.3);
    }
  
    &:active {
      background-color: rgba(40, 54, 69, 0.3);
      color: rgba(255, 255, 255, 0.3);
      border-color: rgba(40, 54, 69, 0.1);
    }
  }
}

.upload__range-controls-bar {
  display: none;
  position: relative;
  height: 2px;
  background-color: $blue;
  width: 240px;
  margin: 0 auto;
  margin-bottom: 34px;

  @include tablet() {
    display: block;
    margin: 0;
    width: 74%;
  }

  @include desktop() {
    width: 63%;
  }
}

.upload__range-controls-toggle {
  position: absolute;
  top: -6px;
  left: 193px;
  width: 14px;
  height: 14px;
  background-color: $white;
  padding: 0;
  border-radius: 50%;
  border: 2px solid $pink;
  cursor: pointer;

  &:active {
    background-color: $pink;
  }
  
  @include tablet() {
    left: 164px;
  }
}
