.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 0 5px;
  min-height: 55px;
  border: 3px solid $white;
  border-radius: 30px;
  line-height: 1;
  font-size: 18px;
  color: $white;
  font-weight: 700;

  cursor: pointer;

  @include desktop() {
    font-size: 21px;
  }
}
