.catalog__title-wrapper {
  background-color: $blue;
  background-image: url("../img/background-mobile.png");
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: -90px;
  padding-bottom: 52px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/background-mobile@2x.png");
  }

  @include tablet() {
    background-image: url("../img/background-tablet.png");
    background-size: cover;
    padding: 0;
    margin-top: -151px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/background-tablet@2x.png");
    }
  }

  @include desktop() {
    background-image: url("../img/background-desktop.png");
    background-size: cover;
    padding: 0;
    margin-top: -161px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/background-desktop@2x.png");
    }
  }
}

.catalog__wrapper {
  @include wrapper;
}

.catalog__list {
  @include list-reset;
  margin-bottom: 44px;

  @include tablet() {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }

  @include desktop() {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    row-gap: 54px;
    column-gap: 20px;
    margin-bottom: 67px;
  }
}

.catalog__item {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;

  @include tablet() {
    margin-bottom: 0;
  }
}

.catalog__item--panorama {
  grid-column: 1/-1;
  margin-left: -20px;
  margin-right: -20px;
  position: relative;

  .catalog__user {
    margin-left: 20px;
    margin-right: 20px;
  }

  @include desktop() {
    margin-left: -134px;
    margin-right: -134px;

    .catalog__user {
      margin-left: 134px;
      margin-right: 134px;
      margin-bottom: 0;
    }
  }

  &::before {
    content: attr(data-text);
    
    position: absolute;
    min-width: 140px;
    padding: 0 5px;
    height: 36px;
    right: 20px;
    background-color: $pink;
    color: $white;
    font-weight: 700;
    font-size: 12px;
    line-height: 24px;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    justify-content: center;

    @include tablet() {
      top: 57px;
    }

    @include desktop() {
      top: 54px;
      right: 130px;
    }
  }
}

.catalog__title {
  font-size: 24px;
  font-weight: 300;
  line-height: 33px;
  text-align: center;
  padding-top: 98px;
  padding-bottom: 6px;
  color: $white;

  @include tablet() {
    font-size: 50px;
    line-height: 68px;
    padding-top: 177px;
    padding-bottom: 127px;
    margin-bottom: 91px;
    text-align: center;
  }

  @include desktop() {
    padding-top: 170px;
    padding-bottom: 89px;
  }
}

.catalog__image {
  width: 100%;
  object-fit: cover;
}

/* .catalog__image--panorama {
  &::after {
    content: attr(data-text);
    position: absolute;
  }
} */

.catalog__description {
  font-size: 14px;
  line-height: 30px;
  text-align: center;
  margin: 0;
  margin-top: 43px;
  margin-bottom: 50px;

  @include tablet() {
    font-size: 18px;
    margin: 0;
    margin-bottom: 77px;
    padding-left: 46px;
    padding-right: 46px;

    br {
      display: none;
    }
  }

  @include desktop() {
    margin-bottom: 88px;
    margin-left: 110px;
    margin-right: 110px;
  }
}

.catalog__user {
  padding-left: 17px;
  background-color: $grey;
  display: flex;
  flex-direction: column;
  flex-grow: 1;

  @include tablet() {
    padding: 24px 15px 12px 30px;
    margin-bottom: 30px;
  }
}

.catalog__user-inner {
  background-color: $grey;

  @include tablet() {
    display: grid;
    grid-template-areas:
      "author likes"
      "text text";
    grid-template-columns: 3fr 2fr;
  }
}

.catalog__user--panorama {
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 50px;
}

.catalog__author {
  font-size: 14px;
  font-weight: 700;
  line-height: 24px;
  text-align: left;
  margin: 0;
  padding-top: 18px;

  @include tablet() {
    padding: 0;
    grid-area: author;
  }
}

.catalog__date {
  font-size: 12px;
  font-weight: 300;
  line-height: 24px;
  padding-left: 3px;
  text-transform: uppercase;
}

.catalog__message {
  font-size: 14px;
  line-height: 24px;
  text-align: left;
  margin-top: 0;
  margin-bottom: 12px;

  @include tablet() {
    grid-area: text;
    margin: 0;
    margin-bottom: 12px;

    br {
      display: none;
    }

    &:first-child {
      margin-bottom: 0;
    }
  }
}

.catalog__user-like {
  margin-bottom: 17px;
  margin-top: auto;
  text-transform: uppercase;

  @include tablet() {
    padding: 0;
    margin: 0;
    margin-top: auto;
    grid-area: likes;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;

    &--panorama {
      justify-content: flex-start;
    }
  }
}

.catalog__button-like {
  margin: 0;
  padding: 0;
  margin-right: 15px;
  border: none;
  cursor: pointer;
}

.catalog__button-like-text {
  font-size: 12px;
  line-height: 24px;
  color: $pink;

  @include tablet() {
    margin-right: 16px;
  }
}