.contacts__wrapper {
  @include wrapper;

  @media (min-width: $md) {
    @include tablet;
  }

  @media (min-width: $lg) {
    @include desktop;
  }
}

.contacts__title {
  font-size: 24px;
  font-weight: 300;
  line-height: 29px;
  text-align: center;
  margin-top: 41px;
  margin-bottom: 34px;

  @include tablet() {
    font-size: 40px;
    font-weight: 300;
    line-height: 54px;
    color: $blue;
    margin-top: 48px;
    margin-bottom: 33px;
  }

  @include desktop() {
    margin-top: 59px;
    margin-bottom: 40px;
    font-size: 50px;
    line-height: 68px;
  }
}

.contacts__container {
  @include tablet() {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 25px;
    row-gap: 42px;
  }

  @include desktop() {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-left: 47px;
  }
}

.contacts__phone {
  @include tablet() {
    display: inline-block;
  }

  @include desktop() {
    order: 1;
  }
}

.contacts__phone-title {
  display: block;
  font-size: 18px;
  font-weight: 700;
  line-height: 25px;
  text-align: center;
  color: $blue;

  @include tablet() {
    margin: 0;
    margin-bottom: 3px;
  }

  @include desktop() {
    margin-bottom: 20px;
  }
}

.contacts__email {
  @include tablet() {
    display: inline-block;
  }

  @include desktop() {
    margin-right: 31px;
    margin-left: -9px;
    order: 3;
  }
}

.contacts__email-title {
  display: block;
  font-size: 18px;
  font-weight: 700;
  line-height: 25px;
  text-align: center;
  color: $blue;

  @include tablet() {
    margin: 0;
    margin-bottom: 3px;
  }

  @include desktop() {
    margin-bottom: 20px;
  }
}

.contacts__address {
  @include tablet() {
    grid-column: 1 / 3;
  }

  @include desktop() {
    display: inline-block;
    order: 2;
    margin-left: 8px;
  }
}

.contacts__adress-title {
  display: block;
  font-size: 18px;
  font-weight: 700;
  line-height: 25px;
  text-align: center;

  @include tablet() {
    margin: 0;
    margin-bottom: 3px;
  }

  @include desktop() {
    margin-bottom: 20px;
  }
}

.contacts__phone-link {
  display: block;
  font-size: 24px;
  font-weight: 300;
  line-height: 33px;
  text-align: center;
  color: $pink;
  margin-bottom: 17px;
  text-decoration: none;

  &:hover {
    color: $blue;
    text-decoration: underline;
  }

  &:active {
    color: rgba(40, 54, 69, 0.3);
  }

  @include tablet() {
    margin: 0;
  }
}

.contacts__email-link {
  display: block;
  font-size: 24px;
  font-weight: 300;
  line-height: 33px;
  text-align: center;
  color: $pink;
  margin-bottom: 21px;
  text-decoration: none;

  &:hover {
    color: $blue;
    text-decoration: underline;
  }

  &:active {
    color: rgba(40, 54, 69, 0.3);
  }

  @include tablet() {
    margin: 0;
  }
}

.contacts__address-link {
  font-style: inherit;
  font-size: 24px;
  font-weight: 300;
  line-height: 33px;
  text-align: center;
  color: $pink;
  margin-bottom: 32px;

  @include tablet() {
    margin-bottom: 25px;
    font-style: inherit;
  }
}

.contacts__map {
  position: relative;
  margin-bottom: 38px;

  @include desktop() {
    margin-bottom: 65px;
  }
}

.contacts__map::before {
  content: "";
  width: 100%;
  height: 33px;
  position: absolute;
  background-color: $white;
  top: -1px;
  left: 0;
  clip-path: polygon(100% 0, 50% 100%, 0 0);
  z-index: 3;

  @include tablet() {
    height: 68px;
  }

  @include desktop() {
    height: 103px;
  }
}

.contacts__google-map {
  width: 100%;
  height: 200px;
  border: none;
  display: block;
  position: relative;
  z-index: 2;

  @include tablet() {
    min-height: 374px;
  }

  @include desktop() {
    min-height: 502px;
  }
}

.contacts__picture-map {
  position: absolute;
  z-index: 1;
  top: 0;
  width: 100%;

  img {
    width: 100%;
    height: 200px;

    @include tablet() {
      height: 374px;
    }

    @include desktop() {
      height: 502px;
    }
  }
}
