.table {
  width: calc(300% - 120px);
  table-layout: fixed;
  border-spacing: 0;
  border-collapse: collapse;
  margin-bottom: 33px;
  line-height: 30px;
  transform: translateX(-256px);

  @include tablet() {
    width: 100%;
    transform: none;
  }
}

.table__header-row {
  height: 80px;
}

.price__rate {
  display: block;
}

.table__header-cell {
  width: calc(100vw - 40px);
  padding: 5px 60px 8px;
  font-weight: 700;
  font-size: 16px;
  line-height: 30px;
  text-align: center;
  text-transform: uppercase;
  color: $white;
  background-color: $blue;

  &:first-child {
    display: none;

    @include tablet() {
      opacity: 0;
      display: table-cell;
    }
  }

  @include tablet() {
    width: 25%;
    padding: 0 28px;
  }

  @include desktop() {
    font-size: 18px;
    padding: 15px 44px 21px;
  }
}

.table__option {
  font-size: 14px;
  line-height: 30px;
  color: $blue;

  @include desktop() {
    font-size: 18px;
  }
}

.table__cell {
  background-color: $white;
  position: relative;
  width: calc(100vw - 162px);
  padding: 13px 20px 16px;
  padding-right: 64px;
  font-weight: 400;
  text-align: left;
  border-bottom: 2px solid $darkgrey;
  text-transform: uppercase;

  &:first-child {
    display: none;

    @include tablet() {
      display: table-cell;
    }
  }

  @include tablet() {
    width: 25%;
    padding: 7px 15px 8px;
    text-transform: none;

    &:not(:first-child) span {
      display: none;
    }
  }

  @include desktop() {
    padding: 15px 21px 11px;
  }

  &::after {
    content: "";
    position: absolute;
    top: 16px;
    right: 17px;
    width: 31px;
    height: 27px;
    background-repeat: no-repeat;
    background-position: center;
    display: block;

    @include tablet() {
      top: 50%;
      left: 50%;
      transform: translate(-15px, -13px);
    }
  }

  &--no::after {
    background-image: url("../img/icon-no.svg");
  }

  &--yes::after {
    background-image: url("../img/icon-yes.svg");
  }
}

.table__cell--central,
.table__header-cell--central {
  border-left: 2px solid $grey;
  border-right: 2px solid $grey;
}

.table__cell--left {
  border-left: 2px solid $grey;
}

.table__header-cell--central {
  position: relative;
  overflow: hidden;

  &::before {
    content: attr(data-text);    
    position: absolute;
    width: 92px;
    height: 24px;
    top: 10px;
    /* right: 210px; */
    left: -26px;
    background-color: $pink;
    color: $white;
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: rotate(-45deg);

    @include tablet() {
      left: -32px;
      height: 20px;
    }

    @include desktop() {
      height: 24px;
      width: 124px;
    }
  }
}
