@charset "UTF-8";
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
  font-weight: 400;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
  font-weight: 700;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
  font-display: swap;
  font-weight: 300;
}
html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-weight: 400;
  color: #000000;
  background-color: #ffffff;
  font-family: "Open Sans", "Arial", sans-serif;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
  user-select: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-header {
  position: relative;
}
.main-header::after {
  content: "";
  width: 100%;
  position: absolute;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.3);
}

.main-header__wrapper {
  width: 100%;
  max-width: 659px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  position: relative;
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  z-index: 2;
  padding-top: 22px;
  padding-bottom: 17px;
}
@media (min-width: 660px) {
  .main-header__wrapper {
    max-width: 1199px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (min-width: 1200px) {
  .main-header__wrapper {
    max-width: 1200px;
    padding-left: 130px;
    padding-right: 130px;
  }
}
@media (min-width: 660px) {
  .main-header__wrapper {
    padding-top: 26px;
    padding-bottom: 40px;
  }
}

.main-header__toggle {
  border: 0;
  padding: 0;
  background: none;
  color: #ffffff;
  width: 50px;
  height: 24px;
  display: grid;
  place-items: end;
}
@media (min-width: 660px) {
  .main-header__toggle {
    margin-top: 16px;
  }
}
@media (min-width: 1200px) {
  .main-header__toggle {
    display: none;
  }
}

.main-header__toggle-burger {
  display: block;
}
.main-header__toggle-burger:hover {
  color: #d22856;
}
.main-header__toggle-burger:active {
  opacity: 0.3;
}

.main-header__toggle-cross {
  display: none;
}

.main-header--showed .main-header__toggle-burger {
  display: none;
}
.main-header--showed .main-header__toggle-cross {
  display: block;
}
.main-header--showed .main-header__toggle-cross:hover {
  color: #d22856;
}
.main-header--showed .main-header__toggle-cross:active {
  opacity: 0.3;
}
.main-header--showed .main-nav {
  top: 100%;
  visibility: visible;
}
.main-header--showed svg {
  display: none;
}
.main-header--showed .main-header__wrapper {
  background-color: #283645;
  box-shadow: 0 1px 0 #000000;
}

.main-header--no-js .main-nav {
  top: 100%;
  visibility: visible;
  order: 1;
  position: static;
}
.main-header--no-js .main-header__toggle {
  display: none;
}
.main-header--no-js .main-header__wrapper {
  padding: 0;
  flex-direction: column;
}
@media (min-width: 1200px) {
  .main-header--no-js .main-header__wrapper {
    flex-direction: row;
    align-items: center;
    padding-left: 110px;
    padding-right: 130px;
  }
}
.main-header--no-js .main-header__logo {
  order: 2;
  padding: 20px 20px;
}
@media (min-width: 1200px) {
  .main-header--no-js .main-header__logo {
    order: 1;
  }
}

